.header {
  font: 14px/1.5 Arial, Helvetica, sans-serif;
  background-color: #292c2f;
  box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.15);
  padding: 20px 40px;
  height: 80px;
  box-sizing: border-box;
}


.header .header-limiter {
  max-width: 1200px;
  text-align: center;
  margin: 0 auto;
}

/* Logo */

.header .header-limiter h1 {
  color: #e9ebf0;
  float: left;
  font: normal 28px Cookie, Arial, Helvetica, sans-serif;
  line-height: 40px;
  margin: 0;
}

.header .header-limiter h1 span {
  color: #8aa7db;
}

/* The header links */

.header .header-limiter a {
  color: #ffffff;
  text-decoration: none;
}

.header .header-limiter nav {
  font: 16px Arial, Helvetica, sans-serif;
  line-height: 40px;
  float: right;
}

.header .header-limiter nav a {
  display: inline-block;
  padding: 0 20px;
  text-decoration: none;
  color: #ffffff;
  font-size: 16px;
  opacity: 0.9;
}

.header .header-limiter nav a:hover {
  opacity: 1;
}

.header .header-limiter nav a.selected {
  color: #608bd2;
  pointer-events: none;
  opacity: 1;
}

.header-limiter nav a.notWorking {
  pointer-events: none;
  opacity: 0.9;
}

.header .header-limiter nav button {
  display: inline-block;
  padding: 0 20px;
  background: black !important;
  border: none;
  color: #ffffff;
  font-size: 16px;
  opacity: 0.9;
  cursor: pointer;
}

.header .header-limiter nav button:hover {
  opacity: 1;
}

/* Create two unequal columns that floats next to each other */
/* Left column */
.leftcolumn {
  float: left;
  width: 75%;
}

/* Right column */
.rightcolumn {
  float: left;
  width: 25%;
  background-color: #f1f1f1;
  padding-left: 0px;
}

/* Add a card effect for articles */
.card {
  background-color: white;
  padding: 20px;
  margin-top: 20px;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Footer */
.footer {
  padding: 20px;
  text-align: center;
  background: #ddd;
  margin-top: 20px;
}

/*Profile Card 1*/
.profile-card-1 {
  font-family: "Open Sans", Arial, sans-serif;
  position: relative;
  float: left;
  overflow: hidden;
  width: 100%;
  color: #ffffff;
  text-align: center;
  /* height:268px; */
  border: none;
  background: #007991;
  background: linear-gradient(to right, #007991, #78ffd6);
}
.profile-card-1 .background {
  width: 100%;
  vertical-align: top;
  opacity: 0.9;
  -webkit-filter: blur(5px);
  filter: blur(5px);
  -webkit-transform: scale(1.8);
  transform: scale(2.8);
}
.profile-card-1 .card-content {
  width: 100%;
  padding: 15px 25px;
  position: absolute;
  left: 0;
  top: 35%;
}
.profile-card-1 .profile {
  border-radius: 50%;
  position: absolute;
  bottom: 55%;
  left: 50%;
  max-width: 100px;
  opacity: 1;
  box-shadow: 3px 3px 20px rgba(0, 0, 0, 0.5);
  border: 2px solid rgba(255, 255, 255, 1);
  -webkit-transform: translate(-50%, 0%);
  transform: translate(-50%, 0%);
}
.profile-card-1 .profile2 {
  border-radius: 50%;
