html, body {
    font-family: 'Roboto', sans-serif;
    padding: 0;
    margin: 0;
  }
  
  #root {
    height: 100vh;
  }
  
  * {
    box-sizing: border-box;
  }
  
  .joinOuterContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    height: 100vh;
    -webkit-align-items: center;
            align-items: center;
    background-color: #1A1A1D;
  }
  
  .joinInnerContainer {
    width: 20%;
    min-width: 300px;
  }
  .joinInputname {
    border-radius: 0;
    padding: 15px 20px;
    width: 100%;
  }
  .joinInputjoin {
    border-radius: 0;
    padding: 15px 20px;
    width: 55%;
  }
  
  .heading {
    color: gold;
    font-size: 2.5em;
    font-family:  Stencil Std,algerian, sans-serif;
    padding-bottom: 10px;
  }
  .subheading {
    color: white;
    font-size: 2 em;
    font-family:  Brush Script Std, Stencil Std,algerian, sans-serif;
    padding-bottom: 10px;
    border-bottom: 2px solid white;
  }
 
  
  .button {
      color: #fff !important;
      text-transform: uppercase;
      text-decoration: none;
      background: #2979FF;
      padding: 15px;
      margin-left: 5%;
      border-radius: 5px;
      border: none;
      width: 40%;
      height: 90%;
  }
  .buttonc {
    color: #fff !important;
    text-transform: uppercase;
    text-decoration: none;
    background: #2979FF;
    padding: 15px;
    margin-top: 10px;
    border-radius: 5px;
    display: inline-block;
    border: none;
    width: 100%;
    height: 100%;
}
  .mt-20 {
    margin-top: 20px;
  }
  
  @media (min-width: 320px) and (max-width: 480px) {
    .joinOuterContainer {
      height: 100%;
    }
  
    .joinInnerContainer {
      width: 90%;
    }
  
  
  }
  
  button:focus {
