.main{
    margin: 0 auto;
    height: 200vh;
}
.navbar{
    background-color: black;
    border-style: solid;
}
.chess{
    color: whitesmoke;
    text-align: left;
    padding: 0px;
    font-family: algerian, sans-serif;
    margin: 0px;
    width: fit-content;
    float: left;
    font-size: 40px;
}

.you{
    text-align: center;
    padding: 0px;
    margin-top: 57px;
    color: whitesmoke;
    position: relative;
    font-family: Arial, Helvetica, sans-serif;

}

.about{
    background-color: black;
    color: aliceblue;
    padding: 5px;
    font-size: 20px;
    cursor: pointer;
    margin: 2px;
    margin: 5px;
    margin-left: 10px;
    border-color: white;   
    border-style: solid;
    float: right;
    border-radius: 5px;
    border-style: hidden;
}
.boardclass{
    position: relative;
    border: 0px;
    padding: 0px;
    margin-left: 100px;
    float: left;
    display: inline;
    
}
.timer{
    margin-top: 100px;
    margin-right: 100px;
    width: 220px;

    padding: 5px;
    float: right;
}

.box{
    position:relative;
    cursor: pointer;
    font-size: larger;
    font-weight: 1000;
    width: 60px;
    min-width: 60px;
    height: 60px;
    text-align: center;
    display: inline;
    display: table-cell; 
    
}
.rotatebutton{
    cursor: pointer;
    margin-left:126px;
    margin-right:120px;
    margin-bottom: 5px;
    margin-top: 5px;

}
.start{
    cursor: pointer;
    color: white;
    background-color: green;
    font-size: 20px;
    
}
.ready{
    cursor: pointer;
    color: blue;
    background-color: whitesmoke;
    margin: 5px;
    font-size: 20px;
}
.stop{
    cursor:pointer;
    margin-left:110px;
    margin-right:110px;
    margin-bottom:5px;
    margin-top:5px;
    width:80px;
    height:35px;
    border-radius: 5px;
    background-color: #cc0a0a ;
    border: 0px;
    font-size: 15px;
    font-weight: 500;
    color: #fff;
}
.stop:hover{
    background-color: #9c0404 ;
}

.timeallocate{
    font-weight: bold;
    border-color: black;
    border-style: solid;
    background-color: whitesmoke;
    text-align: center;
}
.timeallocate1{
    font-weight: bold;
    border-color: black;
    border-style: solid;
    background-color: #99FF00;
    text-align: center;
}
.timeremain{
    font-weight: bold;
    font-size: large;
    color: blue;
}
.scoreboard{
    border-style: solid;
    float: right;
    margin-left: 180px;
    margin-right: 100px;
    width: 200px;
    margin-top: 60px;
    text-align: center;
    background-color: #B5651D;
}
.score{
    font-weight: bold;
    color: black;
    border-style: solid;
    margin: 20px;
    font-size: large;
}
.roomid{
    text-align: center;
    font-size: 20px;
    font-family: sans-serif;
    margin-bottom: 0px;
    color: blanchedalmond;
    font-family: sans-serif, Verdana, Geneva, Tahoma ;
}
.code{
    text-align: center;
    font-size: 20px;
    font-family: sans-serif;
    margin-bottom: 0px;
    color: black;
    font-family: sans-serif, Verdana, Geneva, Tahoma ;
    background-color: white;
    border-radius: 5px;
}
.footer1{
    float: right;
    margin-top: 30%;
    margin-right: 0px;
}

.rot{    
    cursor:pointer;
    margin-left:110px;
    margin-right:110px;
    margin-bottom:5px;
    margin-top:5px;
    width:80px;
    height:35px;
    border-radius: 5px;
    background-color: #06a4af ;
    border: 0px;
    font-size: 15px;
    font-weight: 500;
    color: #fff;
}
.rot:hover{
    background-color: #025f66 ;
}